"use client"

import { points } from "@/lib/product-list"
import Link from "next/link"

export default function Store() {
  return (
    <div className="container mx-auto px-4 py-8 space-y-8 min-h-screen bg-slate-950">
      <div className="text-center space-y-4 opacity-0 animate-fade-in">
        <h1 className="text-4xl sm:text-5xl font-bold mb-4">
          <span className="font-bold text-yellow-400 mb-2">Toko Premium Points</span>
        </h1>
        <p className="text-lg text-gray-300">Beli Premium Points untuk membuka fitur eksklusif</p>
      </div>

      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6">
        {points.map((point) => (
          <Link
            key={point.id}
            href={`/store/product/${point.id}`}
            className="block opacity-0 animate-fade-in"
            style={{ animationDelay: point.delay }}
          >
            <Card className="group relative overflow-hidden border-2 border-orange-400/50 bg-slate-900 transition-all duration-300 hover:border-yellow-400 hover:shadow-lg hover:shadow-yellow-400/20">
      <div className="absolute top-0 left-0 right-0 z-10 flex items-center justify-center pt-6">
        <div className="flex items-center gap-2 rounded-lg bg-gradient-to-r from-orange-400 to-yellow-300 px-4 py-2 transition-all duration-300 group-hover:from-yellow-300 group-hover:to-yellow-200">
          <Star className="h-6 w-6 fill-white text-white" />
          <span className="text-xl font-bold text-white">{amount}</span>
          <span className="text-lg font-semibold text-white">Premium Points</span>
        </div>
      </div>

      {/* Image container */}
      <div className="relative aspect-square">
        <Image src={image || "/placeholder.svg"} alt={`${amount} Premium Points`} fill className="object-cover" />
      </div>

      <div className="absolute bottom-0 left-0 right-0 flex items-center justify-center pb-6">
        <div className="w-4/5 rounded-lg bg-yellow-300 py-3 text-center transition-all duration-300 group-hover:bg-yellow-200">
          <p className="text-2xl font-bold text-slate-900">{price}</p>
        </div>
      </div>
    </Card>
          </Link>
        ))}
      </div>
    </div>
  )
}
